<template>
	<div>
		<message-view @close="close"></message-view>
	</div>
</template>

<script>
	import messageView from './index.vue'
	import {
		getRefList,
		route_uidKey
	} from './ref.js'
	export default {
		name: 'app-message-view',
		components: {
			messageView,
		},
		mounted() {
			this.setupRouteUid()
		},
		onBackPress(options) {
			if (options.from === 'navigateBack') {
				return false;
			}
			// 返回值为 true, 表示阻止该返回事件，不执行默认的返回
			return true;
		},
		data() {
			return {
				route_uid: '',
			}
		},
		methods: {
			setupRouteUid() {
				let routes = getCurrentPages() || []; // 获取当前打开过的页面路由数组
				let currentPage = routes[routes.length - 1]
				this.route_uid = route_uidKey(currentPage)
				// console.log('===------',this.route_uid)
			},
			getCurrentIndex() {
				let routes = getCurrentPages() || []; // 获取当前打开过的页面路由数组
				for (let i = routes.length - 1; i >= 0; i--) {
					let page = routes[i]
					const route_uid = route_uidKey(page)
					if (this.route_uid === route_uid) {
						return i
					}
				}
				return -99
			},
			close(type) {
				let refList = getRefList(this.route_uid)
				let back = refList.every(item => item.isShow === false)
				// console.log('--===')
				if (back) {
					this.backPage()
				}
			},
			backPage() {
				//计算返回的页数 = 总页数 - 当前页数
				let pages = getCurrentPages()
				let currentIndex = this.getCurrentIndex()
				let delta = pages.length - currentIndex
				uni.navigateBack({
					delta: delta, //返回的页数
					animationDuration: 10, //窗口关闭动画的持续时间
					animationType: 'fade-out', //窗口关闭的动画效果
					complete: () => {
						if (currentIndex > 0) {
							//获取上一个组件
							let vm = pages[currentIndex - 1].$vm
							if (typeof vm.jj_closeAllFun === 'function') {
								setTimeout(() => {
									//需要做延时操作，如果不做延时操作，就进行页面跳转，会导致路由丢失
									vm.jj_closeAllFun()
									//重置
									vm.jj_closeAllFun = null
								}, 250)
							}
						}

					}
				})
			},
		},
		destroyed() {
			console.log('--------')
		}
	}
</script>

<style>
	page {
		background: transparent;
		background-color: transparent;
	}
</style>
